<template>
  <div class="container">
    <b-container>
      <h3>
        {{ title }}
        <slot></slot>
      </h3>
      <b-row v-for="r in recipes" :key="r.id">
        <b-col>
          <RecipePreview  class="recipePreview" :recipe="r" :title="title" :myRecipe="myRecipe"/>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import RecipePreview from "./RecipePreview.vue";
export default {
  name: "WatchedRecipePreviewList",
  components: {
    RecipePreview,
  },
  props: {
    title: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      recipes: [],
      myRecipe: false,
    };
  },
  mounted() {
    this.updateWatchedRecipes();
  },
  methods: {
    async updateWatchedRecipes() {
      try {
        if (this.$root.store.Watched_list.length == 0) {
          try {
            const response = await this.axios.get(
              this.$root.store.server_domain + "/users/lastWatchedRecipes"
            );
            const recipes = response.data;
            let Watched_list = [];
            Watched_list.push(...recipes);
            this.$root.store.updateWatchedRecipes(Watched_list);
          } catch (error) {
            console.log(error);
          }
        }
        let Watched_list = this.$root.store.Watched_list;
        this.recipes = [];
        if (Watched_list.length > 0) {
          this.recipes.push(Watched_list[0]);
        }
        if (Watched_list.length > 1) {
          this.recipes.push(Watched_list[1]);
        }
        if (Watched_list.length > 2) {
          this.recipes.push(Watched_list[2]);
        }
        // this.myRecipe = false;
      } catch (error) {
        console.log(error);
      }
    },
  },
};
</script>

