<template>
  <div class="container">    <b-row>
      <!-- Left side of the screen -->
      <b-col class="RandomRecipesCol">
        <RecipePreviewList ref="random" title="Explore This Recipes" class="RandomRecipes" />
        <b-button tag="random-button" @click="$refs.random.updateRecipes()">New Random Recipes</b-button>
      </b-col>
      <!-- Right side of the screen -->
      <b-col class="LastWatchedOrLoginCol">
        <!-- if user is logged in -->
        <LoginPage v-if="!$root.store.username"></LoginPage>
        <!-- if user is not logged in -->
        <WatchedRecipePreviewList v-else ref="watched" title="Last Watched Recipes" class="LastWatchedRecipes"/>
      </b-col> 
    </b-row>
  </div>
</template>

<script>
import RecipePreviewList from "../components/RecipePreviewList";
import WatchedRecipePreviewList from "../components/WatchedRecipePreviewList";
import LoginPage from './LoginPage.vue';
export default {
  components: {
    RecipePreviewList,
    WatchedRecipePreviewList,
    LoginPage
  },
  mounted() {
    this.$refs.random.updateRecipes();
    this.$refs.watched.updateWatchedRecipes();
  },
};
</script>

